import React from "react"
import ApplicationsData from "./ApplicationsData"
import Application from "./Application"
import ApplicationDesktop from "./ApplicationDesktop"
import ApplicationDesktopList from "./ApplicationDesktopList"

const Portfolio = () => {
    
    const ApplicationList = ApplicationsData.map(Ap => <Application key={Ap.name} name={Ap.name} url={Ap.url} description={Ap.description} HTML={Ap.HTML} CSS={Ap.CSS} JavaScript={Ap.JavaScript} Bootstrap={Ap.Bootstrap} Sass={Ap.Sass} ReactJS={Ap.ReactJS} Redux={Ap.Redux} Wide={Ap.Wide} />)
    const ApplicationListDesktop = ApplicationsData.map(Ap => <ApplicationDesktop key={Ap.name} name={Ap.name} url={Ap.url} description={Ap.description} HTML={Ap.HTML} CSS={Ap.CSS} JavaScript={Ap.JavaScript} Bootstrap={Ap.Bootstrap} Sass={Ap.Sass} ReactJS={Ap.ReactJS} Redux={Ap.Redux} Wide={Ap.Wide} />)
    const ApplicationOrderDesktop = ApplicationsData.map(Ap => <ApplicationDesktopList key={Ap.name} name={Ap.name} url={Ap.url} description={Ap.description} HTML={Ap.HTML} CSS={Ap.CSS} JavaScript={Ap.JavaScript} Bootstrap={Ap.Bootstrap} Sass={Ap.Sass} ReactJS={Ap.ReactJS} Redux={Ap.Redux} Wide={Ap.Wide} />)

    return (
        <section className="container">
            <h1>Welcome to my Portfolio</h1>
            <h5>I will be keeping most of my Programming work here on this site, but you can always check it also at my</h5>
            <h4><a className="download-cv" href="https://github.com/dan-sprucek" target="_blank" rel="noopener noreferrer">GITHUB PROFILE</a></h4>
            <div className="portfolio-mobile">
                {ApplicationList}
            </div>
            <div className="portfolio-desktop">
                <div className="portfolio-desktop-list">
                    {ApplicationOrderDesktop}
                </div>
                <div className="portfolio-desktop-apps">
                    {ApplicationListDesktop}
                </div>
            </div>
        </section>
    )
}

export default Portfolio