import React from "react"

const ApplicationDesktop = (props) => {
    return (
        <div className="desktop-app-container">
            <a href={props.url} target="_blank" rel="noopener noreferrer">
                <div className="desktop-app-img-container" id={props.name}>
                    <img className="desktop-app" src={`./sources/${props.name}.png`} alt={props.name} title={props.name}></img>
                </div>
            </a>
            <div>
                <h4 className="mb-0 mt-1"><a className="desktop-app-heading" href={props.url} target="_blank" rel="noopener noreferrer">{props.name}</a></h4>
                <div className="d-flex justify-content-center">
                    {props.HTML && <span className="badge badge-pill badge-info">HTML5</span>}
                    {props.CSS && <span className="badge badge-pill badge-success">CSS</span>}
                    {props.Sass && <span className="badge badge-pill badge-success">Sass</span>}
                    {props.Bootstrap && <span className="badge badge-pill badge-success">Bootstrap</span>}
                    {props.JavaScript && <span className="badge badge-pill badge-danger">JavaScript</span>}
                    {props.ReactJS && <span className="badge badge-pill badge-danger">ReactJS</span>}
                    {props.Redux && <span className="badge badge-pill badge-danger">Redux</span>}
                </div>
            </div>
        </div>
    )
}

export default ApplicationDesktop